import React, {useContext} from 'react';
import {useTags} from 'useTags';
import {useParams,useHistory} from 'react-router-dom';
import Layout from 'components/Layout';
import others from '../icons/others.svg';
import redback from '../icons/back.svg';
import reddelete from '../icons/delete.svg';
import styled from 'styled-components';
import {Input} from '../components/Input';
import {TagContext} from '../context';

type Params = {
    id: string
}
const Wrapper = styled.section`
  .topbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 20px;
    padding: 14px;
    background: #F2F3F5;
    font-size: 20px;
    font-family: PingFang SC;
    font-weight: 600;
    color: #707070;

    > svg {
      width: 30px;
      height: 30px;
    }
  }

  .label {
    line-height: 20px;
    padding: 14px;

    span {
      font-size: 16px;
      font-family: PingFang SC;
      font-weight: 600;
      color: #707070;
      white-space: nowrap;
      margin-right: 12px;
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
    font-size: 13px;
    font-family: PingFang SC;
    font-weight: 600;
    color: #707070;
    display: flex;
    align-items: center;
    justify-content: center;

    > svg {
      width: 40px;
      height: 40px;
    }
  }
`;
const Space = styled.div`
  height: 100px;
`;
const TagEdit: React.FC = (props) => {
    const {findTag, updateTag, deleteTag} = useContext(TagContext)!;
    let {id: idString} = useParams<Params>();
    const tag = findTag(parseInt(idString));
    const tagContent = (tag: { id: number; name: string }) => (
        <div>
            <div className="label">
                <Input label="标签名:" type="text"
                       value={tag.name}
                       onChange={(e) => {
                           updateTag(tag.id, {name: e.target.value,deleted:false});
                       }}
                />
            </div>
            <Space/>
            <div className="footer" >
                <svg onClick={() => onDelete(tag.id)}>
                    <use xlinkHref={`#${reddelete.id}`}></use>
                </svg>
                <span onClick={() => deleteTag(tag.id)}>删除标签</span>
            </div>
        </div>
    );
    const history = useHistory()
    const onclickBack = () => {
      history.goBack();
    };
    const onDelete = (id: number) => {
        deleteTag(id);
        history.push('/tags');
    }

    return (
        <Layout>
            <Wrapper>
                <div className="topbar">
                    <svg onClick={onclickBack}>
                        <use xlinkHref={`#${redback.id}`}></use>
                    </svg>
                    <span>编辑标签</span>
                    <svg></svg>
                </div>
                {tag ? tagContent(tag) : '标签删除成功了～'}
            </Wrapper>
        </Layout>

    );

};

export {TagEdit};