import {useEffect, useRef, useState} from 'react';
import {createdId} from './lib/created';
import {useUpdate} from './Views/useUpdate';


const useTags = () => {
    const [tags, setTags] = useState<{ id: number; name: string; deleted: boolean  }[]>([]);
    useEffect(() => {
      let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]');
      if(localTags.length === 0){
          localTags = [
              {id: createdId(), name: '衣', deleted: false },
              {id: createdId(), name: '食', deleted: false },
              {id: createdId(), name: '住', deleted: false },
              {id: createdId(), name: '行', deleted: false }
          ];
      }
      setTags(localTags);
    },[]);
    useUpdate(() => {
        window.localStorage.setItem('tags',JSON.stringify(tags))
    },[tags])
    const findTag = (id:number) => tags.filter(tag => tag.id === id)[0];
    const findTagIndex = (id: number) =>{
        let result = -1;
        for(let i = 0;i < tags.length;i++){
            if(tags[i].id === id){
                result = i;
                break;
            }
        }
        return result;
    };
    const updateTag = (id:number,{name, deleted}:{ name: string; deleted: boolean }) =>{
        setTags(tags.map(tag => tag.id === id ? {id, name:name, deleted} : tag));
    };
    const deleteTag = (id:number) => {
        const result = window.confirm("确认删除这个标签吗？")
        if (result === true){
            const index = tags.findIndex((i) => i.id === id);
            if (index >= 0) {
                tags[index].deleted = true;
                setTags([...tags]);
            }
        }
    };
    const addTag = () => {
        const tagName = window.prompt('想要添加的标签名称是');
        if (tagName !== null && tagName !== '') {
            setTags([...tags, {id:createdId(),name:tagName, deleted: false }]);
        }
    };
    const getName = (id:number) => {
        const tag = tags.filter(t => t.id === id)[0];
        return tag ? tag.name : '';
    };
    return {tags,getName,addTag,setTags,findTag,updateTag,findTagIndex,deleteTag};
};
export {useTags};
