import React from 'react';

export const TagContext = React.createContext<{
        tags: { id: number; name: string; deleted: boolean }[];
        setTags: (value: { id: number; name: string; deleted: boolean }[]) => void;
        addTag: () => void;
        deleteTag: (id: number) => void;
        updateTag: (id: number, {name}: { name: string; deleted: boolean }) => void;
        findTag: (id:number) => { id: number; name: string; deleted: boolean } | undefined;
    }
    | undefined>(undefined);