import Layout from '../components/Layout';
import React, {useContext} from 'react';
import styled from 'styled-components';
import background1 from '../Image/background1.jpeg';
import cancel from '../icons/cancel.svg';
import 下拉 from '../icons/下拉.svg';
import edittag from '../icons/编辑标签.svg';
import {TagContext} from '../context';
import { Link } from 'react-router-dom';
import {useTags} from '../useTags';

const TagsPageWrapper = styled.section`
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background: #F2F3F5;

  .addbutton {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    
    >svg{
      width: 114px;
      height: 142px;
    }
    .text {
      font-size: 13px;
      font-family: PingFang SC;
      font-weight: 600;
      color: #707070;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      margin-top: 120px;
      box-shadow: 6px 6px 6px #e2e6ef;
      
    }

  }

  .tags {
    margin-top: 12px;
    display: flex;
    align-items: center;
    flex-direction: column;

    > li {
      display: flex;
      align-items: center;
      margin: 8px 11px 0;
      width: 350px;
      background: #FFFFFF;
      border-radius: 15px;
      >a{
        margin-left: 20px;
        flex-grow: 1;
        display: flex;
        align-items: center;
        height: 52px;
        font-size: 20px;
        font-family: PingFang SC;
        font-weight: 400;
        line-height: 24px;
        color: #000000;
      }
      > svg {
        margin-right: 30px;
        width: 25px;
        height: 25px;
      }
      

      span.text {
        font-size: 20px;
        font-family: PingFang SC;
        font-weight: 400;
        line-height: 24px;
        color: #000000;
        flex-grow: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
`;
const Center = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;
const Space = styled.div`
  height: 16px;
`;

function Tags() {
    const {tags, addTag, deleteTag} = useContext(TagContext)!;
    return (
        <Layout>
            <TagsPageWrapper>
                <ol className="tags">
                    {tags.filter((i) => !i.deleted).map((tag, index) =>
                        <li key={tag.id}>
                            <Link to={'/tags/' + tag.id}>
                                <span className="text">{tag.name}</span>
                            </Link>
                            <svg
                                onClick={() => {
                                    deleteTag(tag.id)
                                }}
                            >
                                <use xlinkHref={`#${cancel.id}`}></use>
                            </svg>
                        </li>
                    )}
                </ol>

                <Center>
                    <Space/>
                    <Space/>
                    <div className="addbutton" onClick={addTag}>
                        <svg><use xlinkHref={`#${edittag.id}`}></use></svg>
                        <div className="text">新增标签</div>
                    </div>
                </Center>
            </TagsPageWrapper>
        </Layout>
    );
}

export default Tags;