import 下拉 from '../icons/下拉.svg';
import {RecordItem, useRecords} from './useRecords';
import {useTags} from '../useTags';
import day from 'dayjs';


const StatisticsWrapper = styled.section`
  height: 100%;
  background: #FFFF;
  border-bottom: 4px solid #f7f7f7;
  box-shadow: 0px -3px 3px rgba(0, 0, 0, 0.05);
  flex-direction: column;

  .theme {
    display: flex;
    justify-content: space-around;
    margin: 15px 47px 20px;


    .payment, .income {
      width: 87px;
      height: 42px;
      border-radius: 21px;
      margin-top: 15px;
      display: flex;
      font-size: 20px;
      font-family: PingFang SC;
      font-weight: 800;
      line-height: 28px;
      color: #000000;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .selected {
      background: linear-gradient(180deg, #FFD91C 0%, #FFD816 100%);
    }
  }

  h3 {
    margin: 40px 22px -2px;
    padding-bottom: 10px;
    border-bottom: 4px solid #f7f7f7;
    font-size: 18px;
    font-family: PingFang SC;
    font-weight: 400;
    line-height: 21px;
    color: #949494;
    display: flex;
    justify-content: space-between;
      span {
        font-size: 18px;
        font-family: PingFang SC;
        font-weight: 400;
        line-height: 21px;
        color: #0099FF;
      }
    }
  .list {
    border-bottom: 14px solid #f7f7f7;
    > li {
      padding: 0 30px;
      height: 55px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-top: 1px solid #cdcdcd;

      .tags {
        font-size: 22px;
        font-family: PingFang SC;
        font-weight: 400;
        line-height: 26px;
        color: #000000;
      }
      .note{
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: 800;
        color: #999;
        margin-right: auto;
        margin-left: 16px;
        flex-grow: 1;
      }
      .amount {
        font-size: 22px;
        font-family: PingFang SC;
        font-weight: 500;
        line-height: 30px;
        color: #000000;
      }
    }
  }

`;
const Statistics: React.FC = ({}) => {
    const [billType, setBillType] = useState(BILL_TYPE.PAYMENT);
    const {records} = useRecords();
    const {getName} = useTags();
    const hash:{[K:string] : RecordItem[]} ={}
    const selectedRecords = records.filter(r => r.category === billType );

    selectedRecords.map(r => {
        const key = day(r.createdAt).format('YYYY年MM月DD日')
        if(!(key in hash)){
            hash[key] = []
        }
        hash[key].push(r)
    });
    const array = Object.entries(hash).sort((a,b) => {
        if(a[0] === b[0]) return 0;
        if(a[0] > b[0]) return -1;
        if(a[0] < b[0]) return 1;
        return  0;
    })
    return (
        <Layout>
            <StatisticsWrapper>
                <div className="theme">
                    <div className={['payment', billType === BILL_TYPE.PAYMENT ? 'selected' : ''].join(' ')}
                         onClick={() => setBillType(BILL_TYPE.PAYMENT)}>
                        支出
                    </div>
                    <div className={['income', billType === BILL_TYPE.INCOME ? 'selected' : ''].join(' ')}
                         onClick={() => setBillType((BILL_TYPE.INCOME))}>
                        收入
                    </div>
                </div>
                {array.map(([date,records], index) =>
                    <div key={String(index)}>
                        <h3>
                            {date} <span>¥{records.reduce((pre, cur) => pre + cur.amount, 0)}</span>
                        </h3>
                        <ul className="list">
                            {records.map(r => {
                                return <li key={r.createdAt}>
                                    <div className="tags">
                                        {r.tagIds
                                            .map(tagId => <span key={r.createdAt}>{getName(tagId)}</span>)
                                            .reduce((result,span,index,array) =>
                                                result.concat(index < array.length -1 ? [span, ',']:[span]),[]as ReactNode[])
                                        }
                                    </div>
                                    <div className="note">{r.note}</div>
                                    <div className="amount">
                                        ¥{r.amount}
                                    </div>
                                </li>
                            })}
                        </ul>
                    </div>
                )}
            </StatisticsWrapper>
        </Layout>
    );
};
export default Statistics;
